import React, { useContext } from 'react';
import { ShopContext } from '../../Context/ShopContext';
import all_product from '../../assets/all_product';
import remove from "../../assets/cart_cross_icon.png";

const Cart = () => {
  const { cartItems, removeCart } = useContext(ShopContext);

 
  const subtotal = all_product.reduce((acc, product) => {
    return acc + (cartItems[product.id] || 0) * product.new_price;
  }, 0);

  return (
    <>
      <div className="container py-10">
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white border border-gray-200">
            <thead className="bg-gray-100">
              <tr>
                <th className="py-2 px-4 border-b border-gray-200 text-left font-semibold text-gray-700">Product</th>
                <th className="py-2 px-4 border-b border-gray-200 text-left font-semibold text-gray-700">Title</th>
                <th className="py-2 px-4 border-b border-gray-200 text-left font-semibold text-gray-700">Price</th>
                <th className="py-2 px-4 border-b border-gray-200 text-left font-semibold text-gray-700">Quantity</th>
                <th className="py-2 px-4 border-b border-gray-200 text-left font-semibold text-gray-700">Total</th>
                <th className="py-2 px-4 border-b border-gray-200 text-left font-semibold text-gray-700">Remove</th>
              </tr>
            </thead>
            <tbody>
              {all_product.map((product) => {
                if (cartItems[product.id] > 0) {
                  return (
                    <tr key={product.id}>
                      <td className="py-2 px-4 border-b border-gray-200">
                        <img className="w-10" src={product.image} alt={product.name} />
                      </td>
                      <td className="py-2 px-4 border-b border-gray-200 text-sm">
                        <p>{product.name}</p>
                      </td>
                      <td className="py-2 px-4 border-b border-gray-200">
                        <p>${product.new_price}</p>
                      </td>
                      <td className="py-2 px-4 border-b border-gray-200">
                        <button className="px-4 py-1 bg-gray-100">{cartItems[product.id]}</button>
                      </td>
                      <td className="py-2 px-4 border-b border-gray-200">
                        ${cartItems[product.id] * product.new_price}
                      </td>
                      <td>
                        <img
                          onClick={() => removeCart(product.id)}
                          className="py-2 px-4 border-b border-gray-200 w-11 cursor-pointer"
                          src={remove}
                          alt="remove"
                        />
                      </td>
                    </tr>
                  );
                }
                return null;
              })}
            </tbody>
          </table>
        </div>

        <div className="mt-20 flex flex-col lg:flex-row space-y-8 lg:space-y-0 items-center justify-between">
         <div className=' w-full lg:w-1/2'>
         <h4 className="text-2xl font-serif">Cart Totals</h4>
          <div className="flex items-center justify-between mt-6 border-b border-b-gray-300 py-1">
            <p>Subtotal</p>
            <p>${subtotal}</p> 
          </div>

          <div className="flex items-center justify-between mt-6 border-b border-b-gray-300 py-1">
            <p>Shipping</p>
            <p>Free</p>
          </div>

          <div className="flex items-center justify-between mt-6">
            <p className="font-semibold">Total</p>
            <p className="font-semibold">${subtotal}</p>
          </div>

          <button className='px-3 py-2 bg-orange-600 text-white uppercase mt-8'>proceed to cheickout</button>
         </div>

         <div>
          <p className=' capitalize '>if you have a promo code , enter it here</p>
          <input type="text" placeholder='Promo Code' className=' w-full px-4 py-2 mt-6 bg-gray-100 ' />
          <button className='text-white bg-black px-5 py-2 mt-3'>Submit</button>
         </div>
        </div>
      </div>
    </>
  );
};

export default Cart;
